.bgtransparent {
  z-index: 999;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.185);
}

.boxitempopup {
  position: absolute;
  max-width: 100vh;
  max-height: 100vh;
  background-color: rgb(198, 198, 153);
}

