img {
  width: 100%;
  object-fit: cover;
  border-radius: 10px;
}

img:hover {
  cursor: pointer;
  transition: 200ms;
  filter: brightness(80%);
}

.item {
  max-width: 400px;
  margin: 20px;
  display: block;
}

.item p {
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(160, 95, 100);
  font-family: "Mali", serif;
  font-weight: 500;
  font-style: normal;
}

iframe {
  width: 300;
  height: 300;
  margin: 50px;
}
